  display: block;
  width: 120px; }

.aside-footer__tel {
  font-family: "TT Lakes bold";
  font-size: 24px;
  line-height: 32px;
  display: block;
  width: 210px; }

.aside-footer__footer-languages {
  margin-top: 40px; }

@media screen and (min-width: 1121px) {
  .aside-header__close-btn {
    display: none; }
  .aside-header__link {
    margin-left: 26px; } }

.aside-header {
  display: flex;
  flex-direction: row;
  align-items: center; }

.aside-header__search-btn {
  width: 40px;
  height: 40px;
  margin: 0 24px 0 auto; }

.aside-header__search-btn:hover {
  border-radius: 50%;
  box-shadow: 5px 5px 10px 0px rgba(34, 60, 80, 0.5); }

.aside-header__close-btn:hover {
  border-radius: 50%;
  box-shadow: 5px 5px 10px 0px rgba(34, 60, 80, 0.5); }

.aside-header__close-btn {
  margin: 0 16px 0 24px;
  width: 40px;
  height: 40px; }

.aside-menu {
  display: flex;
  flex-direction: column; }

.aside-menu__item {
  display: block;
  font-family: "TT Lakes regular";
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  padding: 12px 10px;
  position: relative;
  list-style-type: none;
  width: 200px; }

.aside-menu__item:hover::before {
  content: "";
  display: block;
  width: 4px;
  height: 32px;
  background: #41F6D7;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  position: absolute;
  left: -24px;
  top: 6px; }

.aside-menu__item:hover {
  color: #7E7E82;
  cursor: pointer; }

.callback:before {
  content: '';
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 15;
  background: #FFFFFF;
  mix-blend-mode: normal;
  opacity: 0.96; }

.callback--hidden {
  display: none; }

.close-btn {
  border: none;
  background-color: inherit;
  width: 40px;
  height: 40px;
  border-radius: 50%; }

.close-btn__img {
  border-radius: 50%; }

.close-btn__img:hover {
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2); }

.form {
  display: flex;
  flex-direction: column; }

.form__input {
  height: 48px;
  margin: 16px 0;
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 8px;
  padding: 16px 12px; }

.form__input::-webkit-input-placeholder {
  font-family: "TT Lakes regular";
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

.form__input::-moz-placeholder {
  font-family: "TT Lakes regular";
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

.form__input:-ms-input-placeholder {
  font-family: "TT Lakes regular";
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

.form__input::placeholder {
  font-family: "TT Lakes regular";
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

.form__textarea {
  resize: none;
  height: 118px; }

.modal {
  background: #FFFFFF;
  box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
  height: 100%;
  z-index: 20;
  position: fixed;
  top: 0; }

.modal::before {
  content: "";
  display: block;
  width: 1px;
  height: 100%;
  background: #D9FFF5;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  position: absolute; }

.modal__wrapper {
  margin: 0 auto; }

.modal__caption {
  font-family: "TT Lakes bold";
  font-size: 28px;
  line-height: 40px;
  margin: 40px 0px 24px; }

.modal__caption::before {
  content: "";
  display: inline-block;
  background: #41F6D7;
  border-radius: 2px;
  width: 3px;
  height: 40px;
  margin-right: 30px;
  position: relative;
  top: 10px;
  transform: matrix(-1, 0, 0, 1, 0, 0); }

.modal__notice {
  font-family: "TT Lakes regular";
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82; }

.modal__note {
  font-family: "TT Lakes regular";
  font-size: 14px;
  line-height: 16px;
  color: #FF3E79; }

.modal__submit-btn {
  display: flex;
  width: 136px;
  height: 32px;
  background-color: #FF3E79;
  margin: 23px 0 0 auto;
  border: none;
  border-radius: 16px;
  align-items: center;
  justify-content: space-evenly;
  color: #FFFFFF;
  font-family: "TT Lakes regular";
  font-size: 14px;
  line-height: 16px; }

@media screen and (min-width: 320px) and (max-width: 767px) {
  .modal {
    width: 100%;
    height: 100%; }
  .modal__wrapper {
    width: 288px; }
  .modal__close-btn {
    margin: 24px 16px 40px; }
  .modal__caption {
    width: 288px;
    margin: 0 auto; } }

@media screen and (min-width: 768px) {
  .modal {
    width: 440px;
    right: 0; }
  .modal__wrapper {
    width: 376px; }
  .modal__close-btn {
    position: absolute;
    top: 32px;
    right: 472px; } }
